@media (max-width: 1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 50px; } }
	.reasons {
		&_block {
			align-items: flex-start; }
		&_descr {
			width: 300px; } }
	.mobile {
		&_item {
			width: 290px;
			&_subtitle {
				font-size: 22px; } } }
	.choice {
		padding: 55px 0;
		min-height: 840px; }
	.require {
		br {
			display: none; } }
	.footer {
		&_wrapper > div {
			width: 50%;
			&:nth-child(1) {
				width: 25%; }
			&:nth-child(3) {
				width: 25%;
				display: flex;
				justify-content: flex-end; } }
		&_mobile {
			display: flex;
			a {
				margin: 0 3px;
				img {
					width: 100%; } } } } }
@media (max-width: 991px) {
	.title {
		font-size: 28px; }
	.menu {
		&_item {
			padding-right: 8px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&_logo {
			img {
				width: 100%; } }
		&_official {
			font-size: 11px; }
		&_phone {
			font-size: 18px; }
		&_btn {
			margin-top: 21px;
			height: 40px;
			width: 100%; } }
	.promo {
		&_header {
			font-size: 40px; }
		&_subheader {
			font-size: 30px; } }
	.reasons {
		&_block {
			flex-direction: column;
			align-items: center; }
		&_round {
			margin-right: 0; }
		&_descr {
			width: 300px;
			margin-top: 20px; } }
	.choice {
		min-height: 800px;
		&_img {
			width: 100%; } }
	.world {
		min-height: 650px; }
	.footer {
		&_wrapper {
			flex-wrap: wrap; }
		&_wrapper > div {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
			&:nth-child(1) {
				width: 100%; }
			&:nth-child(3) {
				width: 100%;
				justify-content: center; } } } }
@media (max-width: 767px) {
	.title {
		margin-top: 25px; }
	.menu {
		position: fixed;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		background-color: rgba(0,0,0, .8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none;
			margin: 10px 0; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #fff;
			margin-bottom: 2px;
			transition: 0.5s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
	.subheader {
		&_phone {
			display: block;
			margin-top: 10px;
			color: #ffffff;
			font-size: 16px;
			font-weight: 400;
			text-transform: uppercase;
			text-align: center; }
		&_btn {
			display: block;
			width: 200px;
			height: 40px;
			margin: 0 auto;
			margin-top: 10px; } }
	.promo {
		&_header {
			line-height: 45px; } }
	.reasons {
		padding: 40px 0;
		&_descr {
			width: 350px; }
		&_subtitle {
			text-align: center; }
		&_text {
			margin-top: 10px; } }
	.mobile {
		padding: 40px 0; }
	.choice {
		min-height: 600px;
		padding: 40px 0; }
	.require {
		padding: 40px 0; }
	.world {
		min-height: 450px; } }
@media (max-width: 575px) {
	nav {
		position: fixed;
		height: 30px;
		z-index: 3;
		width: 100%; }
	header {
		height: 30px; }
	.title {
		font-size: 16px;
		margin-top: 15px;
		line-height: 16px; }
	.subtitle {
		font-size: 14px;
		line-height: 21px;
		margin-top: 11px; }
	.subheader {
		height: 60px;
		top: 30px;
		&_official {
			display: none; }
		&_logo {
			margin-top: 16px; }
		&_phone {
			font-size: 12px;
			font-weight: 400;
			text-transform: uppercase;
			margin-top: 11px; }
		&_btn {
			width: 110px;
			height: 22px;
			font-size: 9px;
			margin-top: 6px;
			&:hover {} } }

	.promo {
		min-height: 400px;
		padding: 93px 0 35px 0;
		&_descr {
			display: none; }
		&_header {
			font-size: 30px;
			padding: 0 24px;
			line-height: 35px; }
		&_subheader {
			font-size: 20px;
			margin-top: 33px;
			padding: 0 10px;
			line-height: 30px; }
		&_btn {
			width: 210px;
			height: 50px;
			margin-top: 72px;
			font-size: 16px;
			&:hover {} } }
	.menu {
		width: 85%;
		&_item {
			width: 100%;
			text-align: center;
			padding: 5px 0; }
		&_link {
			display: block; } }
	.reasons {
		padding: 22px 0 36px 0;
		&_block {
			margin-top: 26px; }
		&_round {
			width: 100px;
			height: 100px; }
		&_descr {
			width: 100%;
			margin-top: 27px; }
		&_subtitle {
			font-size: 16px; } }
	.title {
		font-size: 16px;
		margin-top: 15px;
		line-height: 16px; }
	.mobile {
		&_item {
			width: 323px;
			height: 254px;
			margin: 27px 1px 30px 0;
			opacity: 0.9; } }
	.car {
		height: 390px;
		.subtitle {
			display: none; }
		.text_1 {
			display: none; }
		.header {
			img {
				width: 300px;
				height: 190px;
				margin: 35px 0 23px; } }
		.footer {
			background-image: none;
			height: 0;
			padding: 0;
			.text {
				margin-top: -100px;
				font-size: 17px;
				font-weight: 300;
				font-stretch: normal;
				font-style: normal;
				letter-spacing: normal;
				text-align: center;
				color: #fff; } } }
	.require {
		margin-top: 100px;
		&_title {
			font-size: 16px; }
		ul {
			li {
				font-size: 14px; } } }
	.map {
		height: 360px;
		.title {
			font-size: 16px; }
		.subtitle {
			font-size: 14px; }
		.link {
			padding-top: 140px; } }
	.footer {
		&_divider {
			width: 253px;
			height: 4px;
			margin: 11px auto;
			background-color: #1fbad6; }
		&_links {
			margin-top: -75px;
			&_main {
				a {
					margin-top: 50px;
					font-size: 10px; } }
			&_sub {
				a {
					margin: 13px 0 13px 2px;
					font-size: 8px;
					font-weight: 300;
					font-stretch: normal;
					font-style: normal;
					letter-spacing: normal;
					text-align: left;
					color: #999; } } }
		&_wrapper {
			position: relative;
			.footer_social {
				position: absolute;
				margin-top: 115px; } }
		&_mobile {
			margin-top: 60px; } } }
